<template>
    <div class="seller" ref="seller">
       <div class="seller-content">
           <div class="overview">
               <h1 class="title">{{seller.name}}</h1>
               <div class="desc border-1px">
                   <star class="star" :size="36" :score="seller.score"></star>
                   <span class="text">{{seller.ratingCount}}</span>
                   <span class="sellCount">月售{{seller.sellCount}}单</span>
               </div>
               <ul class="remark">
                   <li class="block">
                       <h2>起送价</h2>
                       <div class="content">
                           <span class="stress">{{seller.minPrice}}</span>元
                       </div>
                   </li>
                   <li class="block">
                       <h2>商家配送</h2>
                       <div class="content">
                           <span class="stress">{{seller.deliveryPrice}}</span>元
                       </div>
                   </li>
                   <li class="block">
                       <h2>平均配送时间</h2>
                       <div class="content">
                           <span class="stress">{{seller.deliveryTime}}</span>分钟
                       </div>
                   </li>
               </ul>
               <div class="favorite" @click="fav">
                   <span class="icon-favorite" :class="{active:favorite}"></span>
                   <span class="text">{{favoriteText}}</span>
               </div>
           </div>
           <split></split>
           <div class="bulletin">
               <h1 class="title">公告与活动</h1>
               <div class="content-wrapper border-1px">
                   <span class="content">{{seller.bulletin}}</span>
               </div>
               <ul v-if="seller.supports" class="supports">
               <li class="support-item border-1px" v-for="(item,index) in seller.supports" >
                 <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                 <span class="text">{{seller.supports[index].description}}</span>
               </li>
            </ul>
           </div>
           <split></split>
           <div class="pics">
               <h1 class="title">商家实景</h1>
               <div class="pic-wrapper" ref="picwrapper">
                   <ul class="pic-list" ref="piclist">
                     <li class="pic-item" v-for="pic in seller.pics">
                         <img :src="pic" width="120" height="90" alt="">
                     </li>
                   </ul>
                </div>
           </div>
           <split></split>
           <div class="info">
               <h1 class="title border-1px">商家信息</h1>
               <ul>
                   <li class="info-item" v-for="info in seller.infos">{{info}}</li>
               </ul>
           </div>
       </div>
    </div>
</template>
<script>
import star from '../star/star';
import split from '@/components/split/split';
import BScroll from 'better-scroll';
import {saveLocal,loadLocal} from '../../common/js/store.js';

export default{
    data(){
        return{
           favorite:(()=>{
               return loadLocal(this.seller.id,'favorite',false)
           })()

        }
    },
    computed:{
        favoriteText(){
            return this.favorite?'收藏':'未收藏';
        }
    },
    created(){
    this.classMap=['decrease','discount','special','invoice','guarantee'];
    this.$nextTick(()=>{
   if(this.seller.pics){
               let picWidth = 120;
               let margin = 6;
               let width = (picWidth+margin)*this.seller.pics.length-margin;
               this.$refs.piclist.style.width=width+"px";
                this.$nextTick(() => {
                    this.picScroll = new BScroll(this.$refs.picwrapper,{
                        scrollX:true,
                        eventPassthrough:'vertical'
                    })
                })
          }
    })
     
  },
  mounted:function(){
    this.$nextTick(() => {
             if(!this.scroll){
            this.scroll = new BScroll(this.$refs.seller,{
                  click:true
            });
            }else{
                this.scroll.refresh();
            };
           });
           
  },
    props:{
        seller:{
            type:Object
        }
    },
    components:{
        star,
        split
    },
    methods:{
        fav:function(){
                this.favorite = !this.favorite;
            saveLocal(this.seller.id,'favorite',this.favorite);
                
        }
    }
}
</script>
<style lang="stylus">
@import '../../common/stylus/mixin.styl';

.seller
   position absolute
   top 180px
   bottom 0
   left 0
   width 100%
   overflow hidden
   .overview
     position relative
     padding 18px
     .title
       margin-bottom 8px
       line-height 14px
       color rgb(7,17,27)
       font-size 14px
     .desc
       padding-bottom 18px
       line-height 18px
       font-size 0
       border-1px(rgba(7,17,27,0.1))
       .star
         display inline-block
         margin-right 8px
         vertical-align top
       .text, .sellCount
         margin-left 12px
         display inline-block 
         vertical-align top
         font-size 10px
         color rgb(77,85,93)
     .remark
        display flex
        padding-top 18px
        .block
          flex 1
          text-align center 
          border-right 1px solid rgba(7,17,27,0.1)
          &:last-child
            border-right none
          h2
            margin-bottom 4px
            line-height 10px
            font-size 10px
            color rgb(147,153,159)
          .content
            font-weight 800
            line-height 24px
            font-size 10px
            color rgb(7,17,27)
            .stress
              font-size 24px
     .favorite
       position absolute
       width 48px
       right 11px
       top 18px
       text-align center 
       .icon-favorite
           display block 
           margin-bottom 4px
           line-height 24px
           font-size 24px
           color #d4d6d9
           &.active
               color rgb(240,20,20)
       .text
         line-height  10px
         font-size 10px
         color rgb(7,17,27)

       
   .bulletin
      padding 18px 18px 0 18px
      h1
         margin-bottom 8px
         line-height 14px
         color rgb(7,17,27)
         font-size 16px
      .content-wrapper
           padding 0 12px 16px 12px
           border-1px(rgba(7,17,27,0.1))
           .content
             line-height 24px
             font-size 12px
             color rgb(240,20,20)
             font-weight 300
       .supports
        .support-item
          font-size 0
          padding 16px 12px
          border-1px(rgba(7,17,27,0.1))
          &:last-child
            border-none()
        .icon
           display:inline-block
           width:16px
           height 16px
           margin-right:6px
           margin-top -8px
           vertical-align middle 
           background-size :16px 16px
           &.decrease
             bg-img("decrease_4")
           &.discount
             bg-img("discount_4")
           &.special
             bg-img("special_4")
           &.invoice
             bg-img("invoice_4")
           &.guarantee
             bg-img("guarantee_4")
        .text
          display inline-block
          padding 16px 0
          font-size 12px
          font-weight 300
          color rgb(7,17,27)
   .pics
     padding 18px
     .title
       margin-bottom 12px
       line-height 14px
       color rgb(7,17,27)
       font-size 14px
     .pic-wrapper
       width 100%
       overflow hidden 
       white-space nowrap 
       .pic-list
        font-size 0
        .pic-item
         display inline-block
         margin-right 6px
         width 120px
         height 90px
         &:last-child
           margin 0
   .info
     padding 18px 18px 0 18px
     color rgb(7,17,27)
     .title
       padding-bottom 12px
       line-height 14px
       font-size 14px
       border-1px(rgba(7,17,27,0.1))
     .info-item
       padding 16px 12px
       line-height 16px
       border-1px(rgba(7,17,27,0.1))
       font-size 12px
       &:last-child
         border-none()
</style>



