export function saveLocal(id,key,val){
    let seller = localStorage._seller_;
    if(!seller){
        seller = {};
        seller[id]={};
     }else{
        seller = JSON.parse(seller);
        if(!seller[id]){
            seller[id]={};      
        }
    }
    seller[id][key] = val;
    localStorage._seller_=JSON.stringify(seller) 
}

export function loadLocal(id,key,def){
    let seller = localStorage._seller_;
    if(!seller){
        return def;
    }  
    seller = JSON.parse(seller)[id];
    if(!seller){
        return def;
    }
    let ret = seller[key];
    return ret||def;
}


