<template>
  <div class="littleHeader">
    <div class="content-wrapper">
      <div class="avatar">
        <img :src="seller.avatar" alt="" width="64" height="64">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="descripttion">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div v-if="seller.supports" class="support">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text"> {{seller.supports[0].description}}</span>
        </div>
      </div>
      <div @click="Dshow" v-if="seller.supports" class="support-content">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div @click="Dshow" class="bulletin-wrapper">
      <span class="bulletin-title"></span>
      <span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" alt="" width="100%" height="100%">
    </div>
    <transition name="fade">
    <div class="detail" v-show="detailshow">
         <div class="detail-wraper clearfix">
           <div class="detail-main">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wraper">
            <star :size="48" :score="seller.score"></star>
            </div>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="supports">
               <li class="support-item" v-for="(item,index) in seller.supports" >
                 <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                 <span class="text">{{seller.supports[index].description}}</span>
               </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">{{seller.bulletin}}</p>
            </div>
           </div>
         </div>
         <div @click="closeDetail" class="detail-close">
           <i class="icon-close"></i>
         </div>
    </div>
    </transition>
  </div>
</template>

<script>
import star from '../star/star'
export default{
  props:{
    seller:{ }
  },
  data(){
    return {
      detailshow:false
    }
  },
  created(){
    this.classMap=['decrease','discount','special','invoice','guarantee']
  },
  methods:{
   Dshow:function(){
     this.detailshow = true
   },
   closeDetail:function(){
     this.detailshow = false 
   }
  },
  components:{star}
}
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixin.styl'
.littleHeader
  position: relative
  color:#fff
  background :rgba(7,17,27,0.5)
  .content-wrapper
    position: relative
    padding:24px 12px 18px 24px
    font-size:0
    .avatar
      display:inline-block
      font-size :14px
      vertical-align:top
      img 
        border-radius :2px;
    .content
        display:inline-block
        margin-left:16px
      .title
        margin:2px 0 8px 0
        .brand
          display:inline-block
          vertical-align top
          width:30px
          height:18px
          bg-img("brand")
          background-size:30px 18px
          background-repeat :no-repeat
        .name
          margin-left :6px
          font-size :16px
          line-height:18px
          font-weight bold
      .descripttion
          margin-bottom:10px
          line-height:12px
          font-size:12px
      .support
        .icon
           display:inline-block
           vertical-align :top
           width:14px
           height:14px
           margin-right:4px
           background-size:12px 12px 
           background-repeat:no-repeat
           &.decrease
              bg-img("decrease_1")
           &.discount
              bg-img("discount_1")
           &.special
              bg-img("special_1")
           &.invoice
              bg-img("invoice_1")
           &.guarantee
              bg-img("guarantee_1")
        .text
          line-height :12px
          font-size:12px
    .support-content
        position :absolute
        right :12px
        bottom:18px
        padding:0 8px
        height:24px
        line-height :24px
        border-radius :14px
        background:rgba(0,0,0,0.2)
        text-align :center
      .count
         font-size :10px
      .icon-keyboard_arrow_right
         line-height:24px
         font-size :10px
         margin-left:2px
  .bulletin-wrapper
    position: relative
    height:28px
    line-height :28px
    padding:0 22px 0 12px
    white-space:nowrap
    overflow: hidden
    text-overflow :ellipsis
    background :rgba(1,17,27,0.3)
    .bulletin-title
       display :inline-block
       margin-top:8px
       vertical-align :top
       width:22px
       height:12px
       bg-img("bulletin")
       background-size:22px 12px 
       background-repeat:no-repeat
    .bulletin-text
       vertical-align :top
       font-size :10px
       margin:0 4px
    .icon-keyboard_arrow_right
       position:absolute
       font-size:10px
       right:12px
       top:8px
  .background
    position:absolute
    top:0
    left:0
    width:100%
    height 100%
    z-index:-1
  .detail
    position :fixed
    z-index :100
    top:0
    left:0
    width:100%
    height:100%
    overflow :auto
    background :rgba(7,17,27,0.8)
    .detail-wraper
      width:100%
      min-height:100%
      .detail-main
        margin-top:64px
        padding-bottom:64px
        .name
          line-height:16px
          text-align :center
          font-size:16px
          font-weight :700
        .star-wraper
          margin-top:18px
          padding:2px 0
          text-align :center
        .title
          display :flex
          width:80%
          margin:28px auto 24px auto
          .line
            flex:1
            position:relative
            top:-6px
            border-bottom:1px solid rgba(255,255,255,0.2)
          .text
            padding:0 12px
            font-weight :700
            font-size:14px 
        .supports
          width:80%
          margin:0 auto
          .support-item
             padding:0 12px 
             margin-bottom:12px
             font-size:0
             &:last-child
              margin-bottom:0
             .icon
               display:inline-block
               width:16px
               height 16px
               vertical-align :top
               margin-right:6px
               background-size :16px 16px
               &.decrease
                 bg-img("decrease_2")
               &.discount
                 bg-img("discount_2")
               &.special
                 bg-img("special_2")
               &.invoice
                 bg-img("invoice_2")
               &.guarantee
                 bg-img("guarantee_2")
             .text
               line-height :12px
               font-size:12px
        .bulletin
          width:80%
          margin:0 auto
          .content
            padding:0 12px
            line-height :24px
            font-size:12px 
    .detail-close
      position :relative
      width:32px
      height:32px
      margin:-64px auto 0 auto
      clear: both
      font-size :32px
  .fade-enter-active, .fade-leave-active
     transition :opacity 1s
  .fade-enter, .fade-leave-to
     opacity:0







</style>



